import { forwardRef } from 'react';
const ExampleComponent = (props, ref) => {
    const { title, content, ...others } = props;
    return (createElement("div", { ref: ref, className: "ExampleComponent", ...others },
        createElement("h1", null, title),
        content || 'Hello ExampleComponent'));
};
const RefExampleComponent = forwardRef(ExampleComponent);
RefExampleComponent.displayName = 'ExampleComponent';
export default RefExampleComponent;
